.about-container {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    column-gap: 4rem;
}

.about-img {
    justify-self: center;
}

.about-description-1, .about-description-2, .about-description-3 {
    padding: 0 4rem 0 0;
    margin-bottom: 2.5rem;
}

.tech-list {
    column-count: 3;
}

.tech-item::before {
    content: "\2022";
    color: hsl(0, 0%, 20%);
    list-style-type: disc;
    font-weight: bold;
    display: inline-block;
    width: 1em;
}

/* MOBILE DEVICES */
@media screen and (max-width: 992px) {
    .about-container {
        grid-template-columns: 1fr;
        row-gap: 2.5rem;
    }

    .about-img {
        width: 220px;
    }

    .about-data {
        text-align: center;
    }

    .about-description-1, .about-description-2, .about-description-3 {
        padding: 0 5rem;
        margin-bottom: 2rem;
    }
}

@media screen and (max-width: 576px) {
    .about-description-1, .about-description-2, .about-description-3 {
        padding: 0;
    }
}

